<template>
  <div class="cpm-cCountDown">
    <slot :timeData="timeData" v-if="$scopedSlots.default"></slot>
    <template v-else>{{formattedTime}}</template>
  </div>
</template>

<script>
import { formatMSTime } from '../../util/common'
export default {
  name: 'cCountDown',
  props: {
    // 
    time: {
      type: Number,
      default: 0
    },
    // DD-HH-mm-ss-SSS-
    format: {
      type: String,
      default: 'HH:mm:ss'
    },
    // 
    autoStart: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      endTime: 0, // 
      remain: this.time // 
    }
  },
  created () { },
  mounted () { },
  computed: {
    // obj: {d:0, h:0, m:0, s:0, ms:0}
    timeData () {
      return formatMSTime(this.remain)
    },
    //  HH:mm:ss
    formattedTime () {
      return this.parseFormatTime(this.format, this.timeData)
    }
  },
  methods: {
    //  HH:mm:ss  12:12:12
    parseFormatTime (format, timeData) {
      let { d, h, m, s, ms } = timeData
      if (format.includes('DD')) {
        format = format.replace('DD', d)
      } else {
        h += d * 24 // 25DD25:00:00
      }
      if (format.includes('HH')) {
        format = format.replace('HH', `0${h}`.slice(-2))
      } else {
        m += h * 60
      }
      if (format.includes('mm')) {
        format = format.replace('mm', `0${m}`.slice(-2))
      } else {
        s += m * 60
      }
      if (format.includes('ss')) {
        format = format.replace('ss', `0${s}`.slice(-2))
      } else {
        ms += s * 1000
      }
      if (format.includes('SSS')) {
        format = format.replace('SSS', `00${ms}`.slice(-3))
      }
      return format
    },
    // 
    countDownTime () {
      const leftMS = Math.max(this.endTime - Date.now(), 0) //  -  = 
      this.remain = leftMS
      if (leftMS <= 0) {
        window.cancelAnimationFrame(this.rafId)
        this.$emit('onFinish')
        this.countingFlag = false
      }
    },
    // 
    start () {
      const self = this
      if (self.countingFlag) return
      self.endTime = Date.now() + self.remain
      function step () {
        self.rafId = window.requestAnimationFrame(step) // 
        self.countDownTime()
      }
      self.rafId = window.requestAnimationFrame(step)
    },
    // 
    pause () {
      this.countingFlag = false
      window.cancelAnimationFrame(this.rafId)
    },
    // 
    reset () {
      window.cancelAnimationFrame(this.rafId)
      this.countingFlag = false
      this.remain = this.time
      if (this.autoStart) {
        this.start()
      }
    }
  },
  watch: {
    time: {
      immediate: true,
      handler: 'reset'
    }
  }
}
</script>

<style lang="scss" scoped>
</style>



