<template>
  <div :class="['cpm-cButton', `cpm-cButton--${this.size}`, `cpm-cButton--${this.type}`, {
    'cpm-cButton--plain': this.plain,
    'cpm-cButton--circle': this.circle,
    'cpm-cButton--disabled': this.disabled,
  }]" :style="btnStyle" @click="onClick">
    <slot></slot>
  </div>
</template>

<script>
import cIcon from '../cIcon'
export default {
  name: 'cButton',
  components: { cIcon },
  props: {
    // defaultprimarydanger
    type: {
      type: String,
      default: 'default'
    },
    // : smaller/small/normal/large/
    size: {
      type: String,
      default: 'normal'
    },
    // 
    circle: {
      type: Boolean,
      default: false
    },
    // 
    btnStyle: {
      type: Object,
      default: () => { }
    },
    // 
    plain: {
      type: Boolean,
      default: false
    },
    // 
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
    }
  },
  created () { },
  mounted () { },
  computed: {
    // iconObject
    iconOpt () {
      return Object.assign({
        position: 'left', // iconleftright
        type: '', // icon
        render: 'font-class', // font-class
        color: '',
        size: ''
      }, this.icon)
    }
  },
  methods: {
    onClick () {
      this.$emit('onClick')
    }
  }
}
</script>

<style lang="scss" scoped>
.cpm-cButton {
  justify-content: center;
  align-items: center;
  box-sizing: border-box;

  // smaller: 
  &.cpm-cButton--smaller {
    white-space: nowrap;
    display: inline-flex;
    height: 22px;
    font-size: 12px;
    padding: 0 10px;
    &.cpm-cButton--circle {
      border-radius: 11px;
    }
  }
  // small: 
  &.cpm-cButton--small {
    white-space: nowrap;
    display: inline-flex;
    height: 30px;
    font-size: 14px;
    padding: 0 30px;
    &.cpm-cButton--circle {
      border-radius: 15px;
    }
  }
  // normal: 
  &.cpm-cButton--normal {
    white-space: nowrap;
    display: inline-flex;
    height: 45px;
    font-size: 16px;
    padding: 0 50px;
    &.cpm-cButton--circle {
      border-radius: 22.5px;
    }
  }
  // 
  &.cpm-cButton--large {
    display: flex;
    height: 45px;
    font-size: 16px;
    &.cpm-cButton--circle {
      border-radius: 22.5px;
    }
  }

  // default
  &.cpm-cButton--default {
    background-color: #dde2ea;
    color: #8a99ab;
    &.cpm-cButton--plain {
      border: 1px solid #8a99ab;
      background-color: transparent;
      color: #8a99ab;
    }
    &.cpm-cButton--disabled {
      opacity: 0.6;
      cursor: not-allowed;
    }
  }

  // primary
  &.cpm-cButton--primary {
    background-image: linear-gradient(90deg, #508bef 0%, #4b5ffe 100%);
    color: #fff;
    &.cpm-cButton--plain {
      background-image: none;
      border: 1px solid #4b5ffe;
      color: #4b5ffe;
    }
    &.cpm-cButton--disabled {
      opacity: 0.6;
      cursor: not-allowed;
    }
  }

  // danger
  &.cpm-cButton--danger {
    background-image: linear-gradient(90deg, #fe7449 0%, #fb2842 100%);
    color: #fff;
    &.cpm-cButton--plain {
      background-image: none;
      border: 1px solid #fb2842;
      color: #fb2842;
    }
    &.cpm-cButton--disabled {
      opacity: 0.6;
      cursor: not-allowed;
    }
  }
}
</style>



