<template>
  <span :class="['cpm-cRate', disabled ? 'cpm-cRate-disabled' : '']">
    <template v-for="i in max" >
      <cIcon :key="i" :type="currentType(i)" :color="currentColor(i)" :size="size" @onClick="onClick(i)"></cIcon>
    </template>
    <!--  -->
    <slot></slot>
  </span>
</template>

<script>
import cIcon from '../cIcon'
export default {
  name: 'cRate',
  components: { cIcon },
  props: {
    // 0~5v-model
    value: {
      type: Number,
      default: 0
    },
    // star/heart/smile
    type: {
      type: String,
      default: 'star'
    },
    // 
    max: {
      type: Number,
      default: 5
    },
    // size
    size: {
      type: String,
      default: '1.2rem'
    },
    // 
    // 51~234~5
    threshold: {
      type: Array,
      default: () => [2, 4]
    },
    // 
    colors: {
      type: Array,
      default: () => ['#F7BA2A', '#F7BA2A', '#F7BA2A']
    },
    // 
    voidColor: {
      type: String,
      default: '#C6D1DE'
    },
    // 
    disabled: {
      type: Boolean,
      default: false
    },
    // disabled
    disabledVoidColor: {
      type: String,
      default: '#EFF2F7'
    }
  },
  data () {
    return {
    }
  },
  created () {},
  mounted () {},
  methods: {
    onClick (index) {
      if (this.disabled) return
      this.$emit('input', index)
      this.$emit('onChange', index) // 
    },
    // 
    currentType (index) {
      if (this.type === 'star') {
        return index <= this.value ? 'star-filled' : 'star'
      } else if (this.type === 'heart') {
        return index <= this.value ? 'like-filled' : 'like'
      } else if (this.type === 'smile') {
        if (!this.value) return 'meh'
        if (this.value <= this.threshold[0]) { // 
          return index <= this.value ? 'frown-filled' : 'frown'
        } else if (this.value >= this.threshold[1]) { // 
          return index <= this.value ? 'smile-filled' : 'smile'
        } else { // 
          return index <= this.value ? 'meh-filled' : 'meh'
        }
      }
    },
    // 
    currentColor (index) {
      if (index > this.value) { // 
        if (this.disabled) { // 
          return this.disabledVoidColor
        } else {
          return this.voidColor
        }
      } else { // 
        if (this.value <= this.threshold[0]) { // 
          return this.colors[0]
        } else if (this.value >= this.threshold[1]) { // 
          return this.colors[2]
        } else { // 
          return this.colors[1]
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.cpm-cRate{
  font-size: 0;
  display: inline-flex;
  align-items: center;
  &.cpm-cRate-disabled{
    cursor: not-allowed;
  }
}
</style>



