<template>
  <div :class="[
    'cpm-cRange',
    disabled ? 'cpm-cRange-disabled' : '',
    showValue ? 'cpm-cRange-paddingtop' : '',
    calibrateList.length ? 'cpm-cRange-paddingbottom' : '']">
    <slot name="left"></slot>
    <div class="c-range-wrap" ref="content">
      <!--  -->
      <div class="c-range-runway"></div>
      <!--  -->
      <div class="c-range-progress" :style="progressStyle"></div>
      <!--  -->
      <div class="c-range-thumb" ref="thumb" :style="thumbStyle"><span v-if="showValue">{{value}}</span></div>
      <!--  -->
      <div>
        <span class="c-range-calibrate" v-for="(calibrate, index) in calibrateList" :key="index" :style="{left:((calibrate - min) / (max-min) * 100 + '%')}"><i>{{calibrate}}</i></span>
      </div>
    </div>
    <slot name="right"></slot>
  </div>
</template>

<script>
import draggable from './draggable'
export default {
  name: 'cRange',
  props: {
    // v-model
    value: {
      type: Number,
      default: 0
    },
    // 
    min: {
      type: Number,
      default: 0
    },
    // 
    max: {
      type: Number,
      default: 100
    },
    // 
    step: {
      type: Number,
      default: 1
    },
    // 
    disabled: {
      type: Boolean,
      default: false
    },
    // 
    showValue: {
      type: Boolean,
      default: false
    },
    // 
    calibrateList: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
    }
  },
  created () { },
  mounted () {
    const content = this.$refs.content // 
    const thumb = this.$refs.thumb // 

    // 
    const getThumbPosition = () => {
      const contentBox = content.getBoundingClientRect()
      const thumbBox = thumb.getBoundingClientRect()
      return {
        left: thumbBox.left - contentBox.left, // 
        thumbBoxLeft: thumbBox.left // 
      }
    }

    let dragState = {} // 
    draggable(thumb, {
      // touchstart / mousedown
      start: (event) => {
        if (this.disabled) return

        const thumbPosition = getThumbPosition()
        const thumbClickDetailX = event.clientX - thumbPosition.thumbBoxLeft // 
        dragState = {
          thumbStartLeft: thumbPosition.left,
          thumbClickDetailX: thumbClickDetailX
        }
      },
      // touchmove / mousemove
      drag: (event) => {
        if (this.disabled) return

        const contentBox = content.getBoundingClientRect()
        /**
         * event.pageX:   
         * contentBox.left:   
         * dragState.thumbStartLeft:   
         * dragState.thumbClickDetailX:   
         *
         *  1 = 2 + 3 + 4
         * 1234
         */
        const diffX = event.pageX - contentBox.left - dragState.thumbStartLeft - dragState.thumbClickDetailX
        const stepCount = Math.ceil((this.max - this.min) / this.step) // 
        /**
         * dragState.thumbStartLeft + diffX: 
         * (dragState.thumbStartLeft + diffX) % (contentBox.width / stepCount): 
         *
         * 
         */
        const newPostion = (dragState.thumbStartLeft + diffX) - (dragState.thumbStartLeft + diffX) % (contentBox.width / stepCount)
        let newProgress = newPostion / contentBox.width
        // 
        if (newProgress < 0) {
          newProgress = 0
        } else if (newProgress > 1) {
          newProgress = 1
        }
        const currentVal = Math.round(this.min + newProgress * (this.max - this.min)) // 
        this.$emit('input', currentVal)
      },
      // touchend / mouseup
      end: (event) => {
        if (this.disabled) return
        this.$emit('onChange', this.value) // 
        dragState = {} // 
      }
    })
  },
  computed: {
    progress () {
      const value = this.value
      if (typeof value === 'undefined' || value === null) return 0
      return Math.floor((value - this.min) / (this.max - this.min) * 100)
    },
    // 
    progressStyle () {
      return {
        width: `${this.progress}%`
      }
    },
    // 
    thumbStyle () {
      return {
        left: `${this.progress}%`
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.cpm-cRange {
  width: 100%;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  &.cpm-cRange-disabled{
    cursor: not-allowed;
    opacity: .6;
  }
  &.cpm-cRange-paddingtop{
    padding-top: 20px;
  }
  &.cpm-cRange-paddingbottom{
    padding-bottom: 20px;
  }
  .c-range-wrap {
    display: flex;
    flex: 1;
    width: 100%;
    height: 10px;
    border-radius: 5px;
    position: relative;
    background-color: #a9acb1;
    margin: 0 15px;
    .c-range-runway {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border-radius: 5px;
    }
    .c-range-progress {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      border-radius: 5px 0 0 5px;
      background-image: linear-gradient(90deg, #508bef 0%, #4b5ffe 100%);
    }
    .c-range-thumb {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(0, -50%);
      width: 24px;
      height: 24px;
      margin-left: -12px; // 
      border-radius: 50%;
      background-color: #fff;
      box-shadow: 0 0 4px 0 #d8d9dc;
      cursor: all-scroll;
      span{
        position: absolute;
        top: -20px;
        left: 0;
        width: 24px;
        text-align: center;
        font-size: 12px;
      }
    }
    .c-range-calibrate{
      width: 1px;
      height: 10px;
      background-color: #fff;
      position: absolute;
      left: 40%;
      i{
        width: 24px;
        position: absolute;
        top: 20px;
        left: 0;
        text-align: center;
        font-size: 12px;
        margin-left: -12px;
      }
    }
  }
}
</style>



